import React from 'react';
import {connect} from 'react-redux'
import {setAllItems,setColumns,addItems} from '../../../store/action';
import {getAllItem,getMetaData} from '../../../api';
import ColumnSelect from '../../../Components/DataGrid/ColumnSelect'
import Table from '../../../Components/DataGrid/Table';
import WellViolationItem from './item';
import {fields} from './fields';
import axios from 'axios';
import WellWorkFlow from '../Workflow';
import Loader from '../../../Components/Loader';

class WellViolation extends React.Component{

    constructor(props){
          super(props);
          this.state={loading:true,showColumnSelect:false}
    }
    loadColumns=(e)=>{
        var active = !this.state.showColumnSelect;
        this.setState({showColumnSelect:active})

    }
    loopPromise=(i)=>{
       
        getAllItem('WellViolations',100,i).then(d=>{
            if(d.data.length>0){ 
                this.props.dispatch(addItems('WellViolations',d.data));
                this.loopPromise(++i);
            }

        })
    }
    componentDidMount(){
        axios.all([getAllItem('WellViolations',500,1),getMetaData(3)]).then(axios.spread((response,columns)=>{
       
         let wellVisitItems=response.data.filter(itm=>itm.WellVisited.$id).map((k,index)=>k.WellVisited)
         let x= response.data.map(itm=>itm.WellVisited.$ref? {...itm,WellVisited:wellVisitItems.filter(k=>k.$id==itm.WellVisited.$ref)[0]}:itm)
         x=  x.map(itm=>({...itm,Index:itm.WellVisited.WellProfile?itm.WellVisited.WellProfile.Index:10000}));
         this.props.dispatch(setAllItems('WellViolations',x));
       
         let colll= columns.data.map((itm,index)=>Object.assign({},{Header:itm.DisplayName,accessor:itm.Name,type:itm.Type,required:itm.Required,IsMulti:itm.IsMulti,Options:itm.Options?itm.Options.split(';'):null,TitleField:itm.TitleField?itm.TitleField:null,isChecked:(index<5?true:false)}))
         colll.push({accessor:'Index',Header:'شماره پرونده',isChecked:true,type:'Int32'})
         this.props.dispatch(setColumns('WellViolations',colll));
         console.log('data',x);
       console.log('columndata',colll);
        this.setState({loading:false})
        this.loopPromise(2);
        }));
    }
    render(){
      return(   <div>
          <div className='butt-input myrow ' > <input type='button' onClick={this.loadColumns} value='انتخاب ستون ها' /> </div>
            { this.state.showColumnSelect? <ColumnSelect  storeIndex='WellViolations' {...this.props} />:null}
        

        <Table  itemIndex={WellViolationItem} storeIndex='WellViolations' {...this.props} showEdit={false} showRemove={false} showNew={true} workFlow={WellWorkFlow} /> 
        </div>)
    }
}
const mapStateToProps=(state,props)=>({

    columns:state.columns['WellViolations'],items:state.items['WellViolations']
})
export default connect(mapStateToProps)(WellViolation)