import React from 'react';
import {connect} from 'react-redux';
import moment from 'moment-jalaali'
import {getItem,getItemByCode, saveItem} from '../../../api';
import TextArea from './Components/TextArea';
import TextAreaWid from '../../../Components/TextArea/widget';
import CheckBox from './Components/CheckBox';
import CheckBoxWid from '../../../Components/CheckBox/widget';
import Modal from 'react-modal';
import {Criterions} from './Criterions';
import {setDetailsItem,changeItem,setItem,addItem} from '../../../store/action';
import axios from 'axios';
import ItemForm from '../../../Components/ItemForm';
import {getAllItem} from '../../../api'
import Loader from '../../../Components/Loader';
import Select from 'react-select'
import WellProfileItem from '../Profile/item';
import WellVisitItem from '../Visit/item';

const customStyles = {
    content : {
      position: 'absolute',
      top: '40px',
      left: '40px',
      right: '20%',
      bottom: '40px',
      border: '1px solid rgb(204, 204, 204)',
      background: 'rgb(255, 255, 255)',
      overflow: 'auto',
      'border-radius': '4px',
      outline: 'none',
      padding: '20px'
  }
   
    }

class WellViolationItem extends React.Component{
    constructor(props)
    {
        super(props);
        this.state={code:'',showModal:false, loadData:false,loading:false,indexOptions:[{ID:null,Index:'-----'}]}
    }
    handleOpenModal =(e)=> {
        this.setState({ showModal: true });
      }
      
      handleCloseModal =(e)=> {
        this.setState({ showModal: false });
        this.props.dispatch(setItem({},this.props.storeIndex));
        this.setState({selectedItem:{}})
      }
    componentDidMount=(e)=>{
        getAllItem('WellProfiles','ID,Index').then((result)=>{

            this.setState({indexOptions:result.data});
            console.log('options',result);
            this.setState({code: result.data[0].Index});
            console.log('code',this.state.code);
         })
        
           
        //     this.setState({profileItem:result.data});
        //     this.setState({criterions:Criterions})
        //     this.props.dispatch(setDetailsItem(Criterions.map((itm,index)=>(Object.assign({},{Criterion:itm,Description:'',isChecked:false,rowId:index}))),this.props.storeIndex));
        //     }).catch((error)=>console.log(error));
    }
    handleChangeCode=(e)=>{
      this.setState({code:e.target.value})

    }
    loadVisitProfile=(e)=>{
        this.setState({loading:true})
      axios.all([getItemByCode(this.state.code,'WellProfiles'),getItemByCode(this.state.code,'WellVisiteds')]).then(axios.spread((resultProfile,resultVisit)=>{
        if(resultProfile.data && resultVisit.data)
        {
        this.setState({profileItem:resultProfile.data});
        this.setState({visitItem:resultVisit.data});
        console.log('visit',this.state.visitItem);
        console.log('profile',this.state.profileItem);
        
        this.setState({criterions:Criterions})
        this.props.dispatch(setDetailsItem(Criterions.map((itm,index)=>(Object.assign({},{Criterion:itm,Description:'',isChecked:false,rowId:index}))),this.props.storeIndex));
        this.setState({loadData:true});
        this.setState({loading:false})
        }
        else {
            this.setState({loading:false});
            alert('اطلاعات کافی برای این کد پرونده وجود ندارد');
        }
        })).
        catch((error)=>console.log(error));
        this.setState({loading:false});
            alert('اطلاعات کافی برای این کد پرونده وجود ندارد');
        
    }
    handleSubmitInfraction=(e)=>{
        this.setState({loading:true})
        e.preventDefault();
        console.log('rows',this.props.item.rows);
       let violations=this.props.item.rows.filter((itm)=>itm.isChecked);
       let data={ WellVisitedID:this.state.visitItem.ID,NumberInFraction:violations.length,Violations:violations.reduce((acc,itm)=>acc+itm.Criterion+',','')}
       
        saveItem(data,this.props.storeIndex).then((response)=>{
          let entity=this.props.storeIndex+'Items';
          let arrayPost=[];
            violations.forEach(element => {
                let detail={ Criterion:element.Criterion,Description :element.Description,InfractionsWellID:response.data.ID}
               arrayPost.push( saveItem(detail,entity));
            });
            axios.all(arrayPost).then((resp)=>
            {
                alert('آیتم جدید با موفقیت ذخیره شد');
                this.props.dispatch(addItem({...this.props.item,'ID':response.data.ID,WellVisitedID:response.data.WellVisitedID,NumberInFraction:response.data.NumberInFraction,Violations:response.data.Violations,Index:this.state.code},this.props.storeIndex));
                this.setState({loading:false})
                if (!!this.props.closeAfterSave) {
                    this.props.closeAfterSave();
                  }
            //  this.props.dispatch(changeItem(this.props.item,this.props.storeIndex));
            //  this.props.dispatch(setItem({},this.props.storeIndex));
            });
              
        }).catch(
            (error)=>
            console.log('error',error));
      
    }
    render(){




        return(
            <div>

              <div>
                    <Modal 
                    isOpen={this.state.showModal}
                    contentLabel="onRequestClose Example"
                    onRequestClose={this.handleCloseModal}
                    shouldCloseOnOverlayClick={false}
                    style={customStyles}
                    >
                    <div className='close-btt' onClick={this.handleCloseModal}>✖</div>
                    {this.state.storeIndex=='WellProfile'?<WellProfileItem formName='Display' selectedItem={this.state.profileItem} storeIndex='WellProfiles' closeAfterSave={this.handleCloseModal}/>:
                    <WellVisitItem formName='Display' selectedItem={this.state.visitItem} storeIndex='WellVisiteds' closeAfterSave={this.handleCloseModal}/>
                    }
                    </Modal>
               </div>
            
            {this.props.formName=='New'? <div  className='form-medium'>

            <div className='leg-head-form'>
            <div className='labale-form myrow '> کد پرونده  </div> 
            <div className='cod-input myrow'><input type='text' onChange={this.handleChangeCode} /> </div>
           
           {/* <div className='cod-input myrow '><select onChange={this.handleChangeCode}  >              
          {this.state.indexOptions.map((opt,index)=>(<option key={index} value={opt.Index}>{opt.Index}</option>))} 
           </select></div> */}
           
           <div className='butt-input myrow '><input type='button' onClick={this.loadVisitProfile} value='بارگذاری' /></div>
           {/* <div className='butt-input myrow '> <a onClick={ ()=> {this.setState({ showModal: true,storeIndex :'WellProfile',formName:'Display' });}}>اطلاعات شناسنامه ای</a></div>
           <div className='butt-input myrow '><a onClick={()=>{this.setState({showModal:true,storeIndex:'WellVisited',formName:'Display'});}}>اطلاعات بازدید</a></div> */}
            </div>
            {this.state.loadData?
           
               <table className='table-show-item'> 
                   <thead>
                       <tr>
                           <th>عنوان</th><th>شناسنامه ای</th><th>بازدید</th><th>اختلاف</th> <th>عنوان</th><th>شناسنامه ای</th><th>بازدید</th><th>اختلاف</th>
                       </tr>
                   </thead>
                   <tbody>
                       <tr>
                            <td>عمق چاه</td><td>{this.state.profileItem.WellDepth}</td><td>{this.state.visitItem.WellDepth}</td><td>{this.state.visitItem.WellDepth-this.state.profileItem.WellDepth}</td><td>نوع مصرف آب</td><td>{this.state.profileItem.KindConsumptionWater}</td><td>{this.state.visitItem.KindConsumptionWater}</td><td></td>
                       </tr>
                       
                       <tr>
                           <td> سطح زیر کشت</td><td>{this.state.profileItem.AreaUnderCultivation}</td><td>{this.state.visitItem.AreaUnderCultivation}</td><td>{this.state.visitItem.AreaUnderCultivation}</td><td> حجم مجاز برداشت سالانه </td><td>{this.state.profileItem.ApprovedVolume}</td><td>{this.state.visitItem.ApprovedVolume}</td><td>{this.state.visitItem.ApprovedVolume-this.state.profileItem.ApprovedVolume}</td>
                       </tr>
                       <tr>
                              <td>قرائت کنتور</td><td>{this.state.profileItem.NumberMeters}</td><td>{this.state.visitItem.NumberMeters}</td><td>{this.state.visitItem.NumberMeters}</td> <td>چاه در محدوده اراضی قرار دارد یا خیر</td><td>{this.state.profileItem.InArea}</td><td>{this.state.visitItem.InArea}</td><td>{this.state.visitItem.InArea}</td>
                       </tr>
                       <tr>
                              <td>قدرت الکتروموتور</td><td>{this.state.profileItem.PowerlevelElectricity}</td><td>{this.state.visitItem.PowerlevelElectricity}</td><td>{this.state.visitItem.PowerlevelElectricity -this.state.profileItem.PowerlevelElectricity}</td> <td>تاریخ پروانه بهره برداری</td><td>{moment(this.state.profileItem.licenseDate).format('jYYYY/jMM/jDD')}</td><td>{this.state.visitItem.licenseDate}</td><td>{moment(this.state.profileItem.licenseDate).format('jYYYY/jMM/jDD')}</td>
                       </tr>
                       <tr>
                           <td>طول جغرافیایی</td><td>{this.state.profileItem.LongitudeWell}</td><td>{this.state.visitItem.LongitudeWell}</td><td>{this.state.visitItem.LongitudeWell-this.state.profileItem.LongitudeWell}</td> <td>عرض جغرافیایی</td><td>{this.state.profileItem.LatitudeWell}</td><td>{this.state.visitItem.LatitudeWell}</td><td>{this.state.visitItem.LatitudeWell-this.state.profileItem.LatitudeWell}</td>
                       </tr>
                      
                       <tr>
                           <td>آب شیرین کن</td><td>{this.state.profileItem.Desalinationillegal}</td><td>{this.state.visitItem.Desalinationillegal}</td><td></td><td>آب فروشی</td><td>{this.state.profileItem.WaterSupply}</td><td>{this.state.visitItem.WaterSupply}</td><td></td>
                       </tr>
                       <tr>
                           <td>تاریخ حفر و قدمت چاه (جهت چاههای فاقد پروانه)</td><td>{this.state.profileItem.YearDig}</td><td>{this.state.visitItem.YearDig}</td><td></td>
                       </tr>
                   </tbody>
               </table>
            : null
            }
                <br/>
            <div>
                <form onSubmit={this.handleSubmitInfraction}>
                  <h1>  ثبت تخلف </h1>
                    <div className='table-show'>
                    {
                    (this.props.item)&&this.props.item.rows ?
                      this.props.item.rows.map((crt)=><div className='col-item' key={crt.rowId}>
                         <div className='col-table' ><div className='fild-row-item lable' >{crt.Criterion}</div><div className='fild-row-item chekbox ' ><CheckBox render={CheckBoxWid} storeIndex={this.props.storeIndex} internalName='isChecked' rowID={crt.rowId}/></div><div className='fild-row-item texerea ' ><TextArea render={TextAreaWid} rowID={crt.rowId}  storeIndex={this.props.storeIndex} internalName='Description' /></div></div>
                      </div>)
                      :null
                    }
               </div>
                <button>ثبت تخلف</button>
                </form>
            </div>
        </div>:
         <div>
         <ItemForm  storeIndex={this.props.storeIndex} {...this.props} />
        
        
         </div>}
         </div>
        )
    }
    
    }
    const mapStateToProps=(state,props)=>({
       
        item:state.item[props.storeIndex],
       // visitItem:state.item[props.visitIndex]
    })
    export default connect(mapStateToProps)(WellViolationItem)