import React from 'react';
import {connect} from 'react-redux'
import {setAllItems,setColumns, addItems} from '../../../store/action';
import {getAllItem,getMetaData} from '../../../api';
import ColumnSelect from '../../../Components/DataGrid/ColumnSelect'
import Table from '../../../Components/DataGrid/Table';
import WellProfileItem from './item';
import {fields} from './fields';
import axios from 'axios';
import loader from '../../../Components/Loader';
import Loader from '../../../Components/Loader';


class WellProfile extends React.Component{
    constructor(props){
        super(props);
        this.state={showColumnSelect:false,loading:true}
    }
    loadColumns=(e)=>{
        var active = !this.state.showColumnSelect;
        this.setState({showColumnSelect:active})

    }
    loopPromise=(i)=>{
       
        getAllItem('WellProfiles',100,i).then(d=>{
            if(d.data.length>0){ 
                this.props.dispatch(addItems('WellProfiles',d.data));
                this.loopPromise(++i);
            }

        })
    }
    componentDidMount(){  
        const selectedCol=['OwnerName','LongitudeWell','LatitudeWell','Village','Index','CityID' ]
        axios.all([getAllItem('WellProfiles',100,1),getMetaData(1)]).then(axios.spread((response,columns)=>{
            this.props.dispatch(setColumns('WellProfiles',columns.data.map((itm,index)=>Object.assign({},{Header:itm.DisplayName,accessor:itm.Name,type:itm.Type,required:itm.Required,IsMulti:itm.IsMulti,Options:itm.Options?itm.Options.split(';'):null,TitleField:itm.TitleField?itm.TitleField:null,isChecked:(selectedCol.indexOf(itm.Name)!=-1?true:false)}))));
        
            this.props.dispatch(setAllItems('WellProfiles',response.data));
               this.setState({loading:false})  
               this.loopPromise(2);
              
           }));

    }
    render(){

        return( <div>
          
           <div className='butt-input myrow ' > <input type='button' onClick={this.loadColumns} value='انتخاب ستون ها' /> </div>
            { this.state.showColumnSelect?  <ColumnSelect  storeIndex='WellProfiles' {...this.props} />:null}
            <Table   itemIndex={WellProfileItem} storeIndex='WellProfiles' {...this.props} showEdit={true} showRemove={true} showNew={true}  /> 
            </div>)
    }
}
const mapStateToProps=(state)=>({columns:state.columns['WellProfiles'],items:state.items['WellProfiles']})
export default  connect(mapStateToProps)(WellProfile)
