import React from 'react';
import StepOne from './StepOne'
import StepTwo from './StepTwo';
import Loader from '../../../../Components/Loader'
class VisitWorkflow extends React.Component{

    constructor(props){
       super(props);
       this.state={loading:false}
    }
    render(){
        return (!this.state.loading? <form onSubmit={this.handleSubmit}>
            <div className='warper-form'>
                  <div className="row-items"  >    
                      <StepOne isChecked={this.props.selectedItem['DistrictAdministratorApprovalDate']?true:false} />
                  </div>
                  <div className="row-items" >
                      <StepTwo   isChecked={this.props.selectedItem['RegionalWaterCertificateApprovalDate']?true:false} />
                  </div>
              </div>     
             <button type='submit'>ذخیره</button>
           </form>:<div className='sweet-loading'>
      <Loader
      
        sizeUnit={"px"}
        size={150}
        color={'#123abc'}
        loading={this.state.loading}
      />
    </div>
        )}
}
export default VisitWorkflow